<template>
	<view class="userInfo">
		<view class="userInfo-form">
			<u--form labelPosition="left" :model="userForm" labelAlign="right" labelWidth="90">
				<u-form-item label="昵称:" prop="userForm.nickname" borderBottom>
					<u--input v-model="userForm.nickname" border="none"></u--input>
				</u-form-item>
				<u-form-item label="简介:" prop="userForm.intro" borderBottom>
					<u--input v-model="userForm.intro" border="none"></u--input>
				</u-form-item>
				<u-form-item label="邮箱:" prop="userForm.email" borderBottom>
					<u--input v-model="userForm.email" border="none" disabled></u--input>
				</u-form-item>
				<u-form-item label="登录时间:" prop="userForm.lastLoginTime" borderBottom>
					<u--input v-model="userForm.lastLoginTime" border="none" disabled></u--input>
				</u-form-item>
			</u--form>
		</view>
		<view class="userInfo-save">
			<button type="primary" class="save-btn" @tap="updateUserInfo()">保存</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userForm: {
					nickname: uni.getStorageSync('nickname') || null,
					intro: uni.getStorageSync('intro') || null,
					email: uni.getStorageSync('email') || null,
					lastLoginTime: this.$dayjs(uni.getStorageSync('lastLoginTime')).format('YYYY-MM-DD HH:mm:ss') || null
				}
			}
		},
		methods: {
			updateUserInfo() {
				console.log('this.userForm', this.userForm)
				this.$http("/users/info", "PUT", this.userForm).then(res => {
					console.log('res', res)
					if (res.code === 20000) {
						this.saveUserInfoStorage(this.userForm)
						uni.showToast({
							title: '更新成功'
						})
						setTimeout(() => {
							uni.navigateBack()
						},300)
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						})
					}
				})
			},
			saveUserInfoStorage(params) {
				uni.setStorageSync('nickname', params.nickname)
				uni.setStorageSync('intro', params.intro)
				uni.setStorageSync('email', params.email)
				uni.setStorageSync('lastLoginTime', params.lastLoginTime)
			}
		}
	}
</script>

<style lang="scss">
	* {
		margin: 0;
		padding: 0;
	}

	.userInfo {
		width: 100%;
		height: 100vh;
		background-color: #F1F8FA;
	}

	.userInfo-form {
		border-radius: 8rpx;
		background-color: #FFFFFF;
		margin: 20rpx;
		box-shadow: 30rpx #2D2D2D;
	}

	.userInfo-save {
		height: 50rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 30rpx;

		.save-btn {
			background-image: linear-gradient(to top, #4481eb 0%, #04befe 100%);
			font-size: 32rpx;
			width: 80%;
			border-radius: 50rem !important;
		}
	}
</style>
